import selfInput from '@/components/self-input.vue';
import selfButton from '@/components/self-button.vue';
import timer from '@/components/timer/index.vue';

export default {
	data() {
		return {
			leaves: ["A", "B", "C"],
			leaveIdx: 0,
			leaveTime: ''
		}
	},
	components: {
		selfInput,
		selfButton,
		timer
	},
	methods: {
		leaveChange(e) {
			this.leaveIdx = e.target.value
		},
		leaveTimeChange(e) {
			this.leaveTime = e
		}
	}
}
